import React, { memo, useCallback } from 'react';
import {
  AppBar,
  Toolbar,
  IconButton,
  Badge,
  Typography,
} from '@material-ui/core';
import { ShoppingCart } from '@material-ui/icons';
import ExitToAppRoundedIcon from '@material-ui/icons/ExitToAppRounded';
import { useHistory, Link, useLocation } from 'react-router-dom';
import { NavbarText } from 'reactstrap';
import { Alert } from 'rsuite';

import logo from '../../assets/logo.png';
import useStyles from './styles';
import { useCart } from '../../context/profile.context';
import { auth } from '../../misc/firebase';

const Navbar = () => {
  const classes = useStyles();
  const cart = useCart();
  const history = useHistory();
  const location = useLocation();

  const cartItemsTotal = cart.reduce((total, item) => total + item.quantity, 0);

  const gotoBasket = () => {
    if (cartItemsTotal > 0) {
      history.push('/basket');
    }
  };

  const onSignOut = useCallback(() => {
    auth.signOut();

    Alert.info('Signed out', 4000);
  }, []);

  return (
    <>
      <AppBar position="fixed" className={classes.appBar} color="inherit">
        <Toolbar>
          {/* typography for logo and our brand */}
          <NavbarText>
            <Link
              to="/"
              className="d-flex align-items-center"
              style={{ textDecoration: 'none' }}
            >
              <Typography
                variant="h6"
                className={classes.title}
                color="inherit"
              >
                <img
                  src={logo}
                  alt="SmartShopping"
                  height="25px"
                  className={classes.image}
                />
                SmartShopping
              </Typography>
            </Link>
          </NavbarText>
          <div className={classes.grow} />
          Sign Out
          <div className={classes.button}>
            <IconButton aria-label="Show cart items" color="inherit">
              <Badge onClick={onSignOut} color="secondary">
                <ExitToAppRoundedIcon />
              </Badge>
            </IconButton>
          </div>
          <div className={classes.button}>
            {location.pathname === '/' ||
            location.pathname === '/headphones' ||
            location.pathname === '/keyboards' ? (
              <IconButton aria-label="Show cart items" color="inherit">
                <Badge
                  onClick={gotoBasket}
                  badgeContent={cartItemsTotal}
                  color="secondary"
                >
                  <ShoppingCart />
                </Badge>
              </IconButton>
            ) : null}
          </div>
        </Toolbar>
      </AppBar>
    </>
  );
};

export default memo(Navbar);
